            <div class="content">
                <div class="listContent">
                    <div class="listWidth">
                        <yd-list theme="5">
                            <yd-list-item type="div" v-for="item in companyList" :key="item.title" v-on:click.native="openDetail(item,0)">
                                <span slot="title" class="span_title" v-text="item.title" v-on:click="openDetail(item)">
                                        </span>
                                <yd-list-other slot="other">
                                    <div>
                                        <span class="span_publisher">发布者：{{item.dept}}</span>
                                        <span class="span_date" v-text="item.createtime"></span>
                                    </div>
                                </yd-list-other>
                            </yd-list-item>
                        </yd-list>
                    </div>
                </div>
                <div class="more_news">
                    <div class="click_more2" v-on:click="moreCompanyNews()" v-if="comflag">
                        查看更多历史公告
                    </div>
                </div>
            </div>
        </div>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    name: 'news',
    data() {
        return {
            unionPayList: [],
            companyList: [],
            upflag: true,
            comflag: true,
            pageNum: 1,
            nowDate: ''
        }
    },
    mounted() {
        this.nowDate = `${new Date().getFullYear()}年${new Date().getMonth() + 1}月${new Date().getDate()}日`
        // this.unionPayNews();
        this.companyNews();
        document.setTitle('公司公告');
        this.current = Math.round(new Date() / 1000);
        this.oneWeek = 604800;
    },
    computed: {
        ...mapGetters([
            'getUserInfo'
        ])
    },
    methods: {
        openDetail(item, leibie) {
            let vm = this;
            let timestamp = Date.parse(item.createtime.replace(/-/g, '/'));
            let timestamp2 = timestamp / 1000;
            vm.$router.push('/detail/' + item.urlPath.split('docId=')[1].split('&')[0] + '/' + item.dept + '/' + timestamp2 + '/' + leibie);
        },
        // 银联头条
        unionPayNews() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/AddressBook/affiche/list', {
                name: vm.getUserInfo.ssotoken,
                channelId: 3029,
                pageSize: 5
            }, function(res) {
                var data = res.jsonData;
                vm.unionPayList = [...vm.unionPayList, ...data];
            });
        },
        // 银联要闻查看更多
        moreNewsUnionPay() {
            var vm = this;
            vm.pageNum++;
            vm.get(vm.conf.portalMobile + '/API/AddressBook/affiche/list', {
                name: vm.getUserInfo.ssotoken,
                pageSize: 5,
                channelId: 3029,
                pageNo: vm.pageNum
            }, function(res) {
                var data = res.jsonData;
                vm.unionPayList = [...vm.unionPayList, ...data];
                if (data.length == 0) {
                    vm.upflag = false;
                }
            });
        },
        // 公司公告查看更多
        companyNews() {
            var vm = this;
            vm.get(vm.conf.portalMobile + '/API/AddressBook/affiche/list', {
                name: vm.getUserInfo.ssotoken,
                pageSize: 5
            }, function(res) {
                var data = res.jsonData;
                vm.companyList = [...vm.companyList, ...data];
            });
        },
        // 公司公告查看更多
        moreCompanyNews(num) {
            var vm = this;
            vm.pageNum++;
            vm.get(vm.conf.portalMobile + '/API/AddressBook/affiche/list', {
                name: vm.getUserInfo.ssotoken,
                pageSize: 5,
                pageNo: vm.pageNum
            }, function(res) {
                var data = res.jsonData;
                vm.companyList = [...vm.companyList, ...data];
                if (data.length == 0) {
                    vm.comflag = false;
                }
            });
        }
    }
}
</script>
<style scoped lang="less">
.anew {
    height: 100%;
    background-color: #fff;
}

.content {
    height: 100%;
    background-color: #fff;
    /*border-top: .2rem solid #f8f8f8;*/
    .yd-list-theme5 .yd-list-item:after {
        background-image: none;
    }
}

.listWidth {
    padding-left: 0;
}

.yd-list-mes {
    padding-left: .32rem;
}

.news_date {
    padding-left: 0.3rem;
    background: #a3d6ff;
    height: 0.59rem;
    color: #fff;
    font-size: 0.28rem;
    line-height: 0.59rem;
}

.listWidth .yd-list-item .span_logo:after {
    width: 0.8rem;
    height: 0.4rem;
    content: "";
    position: absolute;
    top: 0;
    right: 0.6rem;
    background: url(../../../static/images/trs_yl_02.png) no-repeat;
    background-position: 0.4rem 0.05rem;
    background-size: 0.4rem;
    overflow: hidden;
}

.span_title {
    font-size: 0.32rem;
    display: block;
    width: 5.7rem;
    height: 0.45rem;
    overflow: hidden;
    line-height: 0.45rem;
    white-space: nowrap;
    text-overflow: ellipsis;
    color: #333;
    font-weight: 100;
    margin-bottom: .2rem;
}

.title {
    overflow: hidden;
    width: 5.5rem;
    height: 0.45rem;
    line-height: 0.45rem;
    font-size: 0.3rem;
    display: block;
}

.span_publisher {
    font-size: 0.28rem;
    width: 2.9rem;
    display: block;
    float: left;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}

.span_date {
    font-size: 0.28rem;
    /*padding-left: 0.8rem;*/
    padding-right: 0.32rem;
}

.span_time {
    font-size: 0.26rem;
}

.read_btn {
    width: 1.04rem;
    height: 0.44rem;
    background: url(../../../static/images/trs_yl_0921.png) #ffa740;
    background-repeat: no-repeat;
    background-size: 0.26rem;
    background-position: 0.06rem 0.06rem;
    border-radius: 0.5rem;
    position: fixed;
    bottom: 0.3rem;
    right: 0.5rem;
    z-index: 5;
}

.read_btn02 {
    background: url(../../../static/images/trs_yl_0921.png) #4aa9f6;
    background-repeat: no-repeat;
    background-size: 0.26rem;
    background-position: 0.06rem 0.06rem;
}

.read_btn .read_btn_a {
    color: #fff;
    font-size: 0.22rem;
    line-height: 0.44rem;
    padding-left: 0.4rem;
    width: 1.04rem;
    height: 0.44rem;
    display: block;
}

.titleColor {
    color: #999;
}

.fontColor {
    color: #999;
}

.click_more2 {
    width: 3.3rem;
    height: 0.7rem;
    color: #59a5fd;
    text-align: center;
    font-size: 0.32rem;
    line-height: 0.7rem;
    margin: 0 auto;
}
</style>


